



















import Vue from "vue";
import Numberpad from "@/components/Money/Numberpad.vue";
import FormItem from "@/components/Money/FormItem.vue";
import Tags from "@/components/Money/Tags.vue";
import { Component } from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
import recordTypeList from "@/constants/recordTypeList.ts";
import store from "../store/index";

// type RecordItem = {
//   tags: string[];
//   notes: string;
//   type: string;
//   amount: number; //数据类型
//   createdAt?: Date; //类、构造函数
// };
@Component({
  components: { Tabs, Numberpad, FormItem, Tags },
  computed: {
    recordList() {
      return this.$store.state.count;
    },
  },
  // count() {
  //   return store.count;
  // },
  // recordStore() {
  //   return store.recordList; //地址 recordList复制到recordList
  // },
})
export default class Money extends Vue {
  // add() {
  //   store.addCount();
  // }
  get recordList() {
    return this.$store.state.recordList;
  }

  recordTypeList = recordTypeList;
  record: RecordItem = {
    tags: [],
    notes: "",
    type: "-",
    amount: 0,
  };
  created() {
    this.$store.commit("fetchRecords");
  }
  onUpdateNotes(value: string) {
    this.record.notes = value;
  }
  onUpdateType(value: string) {
    this.record.type = value;
  }
  onUpdateAmount(value: string) {
    this.record.amount = parseFloat(value);
  }
  saveRecord() {
    if (!this.record.tags.length || this.record.tags.length === 0) {
      return window.alert("请至少选择一个标签");
    }
    // store.createRecord(this.record);
    this.$store.commit("createRecord", this.record);
    if (this.$store.state.createRecordError === null) window.alert("已保存");
    this.record.notes = "";
  }
}
